import React, { useState, useEffect } from 'react';
import { useLocation } from '@reach/router';
import { Link } from 'gatsby';
import { routes } from '../constants';

const Header = () => {
  const [isActive, setIsActive] = useState();
  const [useClass, setUseClass] = useState('');
  const [scrolling, setScrolling] = useState(false);
  const { pathname } = useLocation();

  const toggleMobileMenu = () => {
    if (isActive) setUseClass('hidden');
    else setUseClass('active');

    setIsActive(!isActive);
  };

  const changeClass = () => {
    const scrollPosY = window.pageYOffset || document.body.scrollTop;

    if (scrollPosY > 30) setScrolling(true);
    else if (scrollPosY <= 30) setScrolling(false);
  };

  useEffect(() => {
    changeClass();

    const body = document.querySelector('body');
    if (isActive) body.style.overflow = 'hidden';
    else setTimeout(() => (body.style.overflow = ''), 400);

    window.onscroll = changeClass;
  });

  return (
    <header
      className={`sticky top-0 z-50 bg-white py-6 ${
        scrolling ? 'border-b border-border mt-0' : 'mt-4'
      }`}
    >
      <div className="contain flex items-center justify-between">
        <Link
          className={`font-heading text-xl tracking-wider z-50 transition-colors duration-300 ${
            isActive ? 'text-white' : 'text-primary'
          }`}
          to={routes.HOME}
          onClick={isActive ? toggleMobileMenu : null}
        >
          Safdar Jamal
        </Link>
        <div
          role="button"
          aria-label="Toggle navigation"
          tabIndex={0}
          className={`hamburger relative block z-50 pointer focus:outline-none ${
            isActive ? 'active' : ''
          }`}
          onClick={toggleMobileMenu}
          onKeyDown={event =>
            event.key === 'Enter' ? toggleMobileMenu() : null
          }
        >
          <div className="bg-primary"></div>
          <div className="bg-primary"></div>
          <div className="bg-primary"></div>
        </div>
        <nav className={useClass}>
          <div className="menu mt-32 contain flex flex-col font-heading text-3xl tracking-wider items-end">
            <Link
              to={routes.HOME}
              className={`mb-8 hover:text-white ${
                pathname === routes.HOME ? 'text-white active' : 'text-offgray'
              }`}
              onClick={isActive ? toggleMobileMenu : null}
            >
              Home
            </Link>
            <Link
              to={routes.ABOUT}
              className={`mb-8 hover:text-white ${
                pathname === routes.ABOUT ? 'text-white active' : 'text-offgray'
              }`}
              onClick={isActive ? toggleMobileMenu : null}
            >
              About
            </Link>
            <Link
              to={routes.PORTFOLIO}
              className={`mb-8 hover:text-white ${
                pathname === routes.PORTFOLIO
                  ? 'text-white active'
                  : 'text-offgray'
              }`}
              onClick={isActive ? toggleMobileMenu : null}
            >
              Portfolio
            </Link>
            <Link
              to={routes.CONTACT}
              className={`mb-8 hover:text-white ${
                pathname === routes.CONTACT
                  ? 'text-white active'
                  : 'text-offgray'
              }`}
              onClick={isActive ? toggleMobileMenu : null}
            >
              Contact
            </Link>
          </div>
        </nav>
      </div>
    </header>
  );
};

export default Header;
